/* ===================================== 
   Navbar Styles
======================================== */

.btn-nav:before
{
    display: block;
    content: '';
    position: absolute;
}

.btn-nav
{
    width: 25px;
    height: 17px;
    border-top: solid 3px #797e83;
    border-bottom: solid 3px #797e83;
    position: relative;
}

.btn-nav:before
{
    width: 25px;
    height: 3px;
    background: #999da1;
    top: 4px;
}

.btn-nav
{
    line-height: 0;
    cursor: pointer;
}