/* ===================================== 
   Page Styles
======================================== */

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Varela+Round&display=swap');

*
{
	box-sizing: border-box;
}

body
{
    background-color: $color-primary;
    color: $color-text-dark;

    font-family: Poppins, sans-serif;
    font-size: 1rem;
	letter-spacing: 0.4px;

    padding: 0;
    margin: 0;
}

.container > *
{
    color: #fff;
    text-shadow: 0 2px 0 rgba(110,133,156,0.12);
    padding: 0.85em;
    border: solid 1px rgba(110,133,156,0.15);
}

.section
{
	padding: 50px 15px;
}

#Welcome
{
    background-color: $color-secondary;
    color: $color-text-light;

	h2
	{
		font-size: 2.25rem;
		font-weight: 600;
		color: $color-text-light;
	}
}

#SignUp
{
    background-color: $color-form;
}

.link-phone
{
    width: 28px;
    height: 28px;

    img
    {
        width: 28px;
        height: 28px;
    }
}

