}

h2
{
	font-size: 2.7rem;

	font-weight: 600;
    margin: 0 0 15px;
	color: #333645;
}

a
{
	color: inherit;
}

p {
    line-height: 1.875rem;
    margin-bottom: 25px;
}

/* ===================================== 
   Grid Layout
======================================== */

.container
{
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 100px minmax(200px, 1fr) 100px 100px; 
    grid-gap: 10px;
    grid-template-areas:
        "header"
        "main"
        "aside" 
        "footer";
  }
  
  header
  {
    grid-area: header;
  }
  
  main
  {
    grid-area: main;
  }
  
  aside
  {
    grid-area: aside;
  }
  
  footer
  {
    grid-area: footer;
  }